.card {
  /* Add shadows to create the "card" effect */
  -webkit-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;

  padding-bottom: 1em;
  padding-top: 1em;
  background-color: black;
  border-radius: 5px; /* 5px rounded corners */
  -ms-flex: 1 1;
      flex: 1 1;

  margin-left: auto;
  margin-right: auto;
}
.cardContainer {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  -ms-flex-item-align: center;
      align-self: center;
  margin-bottom: 10em;
  z-index: 2;
  -ms-flex: 1 1;
      flex: 1 1;
}

/* Add some padding inside the card container */

.photoTop {
  position: absolute;
  top: -8em;
  left: -4em;
  height: 15em;
  width: 23em;
  -o-object-fit: cover;
     object-fit: cover;

  border: 5px solid rgb(255, 255, 255);
  -webkit-box-shadow: 0 10px 24px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 0 10px 24px 0 rgba(0, 0, 0, 0.3);
  z-index: 25;
}

.photoBottom {
  position: absolute;
  bottom: -10em;
  left: -5em;
  height: 15em;
  width: 23em;
  -o-object-fit: cover;
     object-fit: cover;

  border: 5px solid rgb(255, 255, 255);
  -webkit-box-shadow: 0 10px 24px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 0 10px 24px 0 rgba(0, 0, 0, 0.3);
  z-index: 25;
}

.card-title {
  text-align: right;
  color: black;
}

.card-description {
  text-align: left;
  color: black;
}



